import { ItemProduct } from '../ItemProduct/ItemProduct';
import React from 'react';
import { Pagination } from '../Pagination/Pagination';
import { useSearchParams } from 'react-router-dom';
import { useAppSelector } from '../../../../../store';
import { $ } from '../../../../../components';

import '../../style/Goods.scss';
import { ProductT } from '../../../../../store/goodsSlices/goods.config';

type ProductTypes = {
  like?: boolean;
};

const ProductTable = ({ like = false }: ProductTypes): JSX.Element => {
  const { goods, pages } = useAppSelector((state) => state.device);
  const { products } = useAppSelector((state) => state.favorite);
  const [searchParams] = useSearchParams();

  const blockProd = () => {
    const rows: JSX.Element[] = [];
    let productsNew;

    if (like) {
      productsNew = products;
    } else {
      if (searchParams.has('sale')) {
        productsNew = goods.filter((item) => item.sale > 0);
      } else {
        productsNew = goods;
      }
    }

    if (products.length <= 0) {
      $('.product__table').remove('load');
    }

    productsNew.map((product: ProductT) => {
      rows.push(<ItemProduct product={product} key={product.name} />);
    });

    return rows;
  };

  return (
    <div className='product__table load'>
      <div className='product__goods'>{blockProd()}</div>
      {+pages > 1 && !like && <Pagination />}
    </div>
  );
};

export default ProductTable;
