import './GoodsRow.scss';

const GoodsRow: React.FC<GoodsRowConf> = ({ classname = '', children }) => {
  return (
    <div className='goodsrow'>
      <div className='container'>
        <div className='goodsrow__row'>
          <div className={`goodsrow__container ${classname}`}>{children}</div>
        </div>
      </div>
    </div>
  );
};

export default GoodsRow;

type GoodsRowConf = {
  classname?: string;
  children: React.ReactNode;
};
