import { useEffect, useState } from 'react';
import ProductTable from '../Shop/Goods/components/ProductTable/ProductTable';
import { LikeTuner } from './LikeTuner';
import { LikePagination } from './LikePagination';
import { useAppDispatch, useAppSelector } from '../../store';
import { favoriteLimit } from '../../store/goodsSlices/favoriteSlice';
import GoodsRow from '../Shop/GoodsRow/GoodsRow';
import { LoadingWin, Title } from '../../components';
import { renderSortMenu } from '../../jsFunction/addClassSort&Search';

import './style/LikesGoods.scss';

const LikesGoods: React.FC = () => {
  const dispatch = useAppDispatch();
  const { products, productsLimit, loading } = useAppSelector(
    (state) => state.favorite
  );
  const [limit, setLimit] = useState<string>('8');

  async function fetching() {
    dispatch(favoriteLimit({ limit }));
  }

  async function fetchingTwo() {
    if (products.length != productsLimit.length) {
      const count = productsLimit.length - 1;

      if (count >= 0) {
        dispatch(favoriteLimit({ limit }));
      }
    }
  }

  //   useEffect(() => {
  //     window.addEventListener('click', renderSortMenu);
  //     return function cleanup() {
  //       window.removeEventListener('click', renderSortMenu);
  //     };
  //   }, []);

  useEffect(() => {
    fetching();
    document.title = 'Favorite goods';
  }, []);

  useEffect(() => {
    fetchingTwo();
  }, [products.length]);

  return (
    <>
      <div className='likesGoods'>
        <div className='indentstart'></div>
        <Title name='Favorites' />

        <>
          <GoodsRow classname='like'>
            <LikeTuner setLimit={setLimit} limit={limit} />
            <LoadingWin isLoading={loading}>
              <ProductTable like={true} />
            </LoadingWin>
            {+products.length > +limit &&
              +products.length > +productsLimit.length && (
                <LikePagination limit={limit} />
              )}
          </GoodsRow>
        </>
      </div>
    </>
  );
};

export default LikesGoods;
