import { useLocation, useNavigate, useSearchParams } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../../../store';
import { changePage } from '../../../../../store/goodsSlices/devicesAPI/deviceSlice';

import './style/Pagination.scss';

export type PaginationConf = {
  children: string;
};

export const Pagination: React.FC = (): JSX.Element => {
  const { pages, page } = useAppSelector((state) => state.device);
  const dispatch = useAppDispatch();

  const getPage = () => {
    const rows = new Map();
    for (let item = 1; item <= +pages; item++) {
      rows.set(
        item,
        <PaginationList key={item}>{item.toString()}</PaginationList>
      );
    }
    return Array.from(rows.values());
  };

  const pathnamepage = useChangePathname();

  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    let page = pages;

    if (
      ((e.target as HTMLElement)?.closest('.pagination__back') as HTMLElement)
        .dataset.back
    ) {
      page = +page - 1 + '';
    } else {
      page = +page + 1 + '';
    }

    if (+page >= 1 && +page <= +pages) {
      pathnamepage('page', page.toString());
      dispatch(changePage({ page: page.toString() }));
      document.documentElement.scrollIntoView({
        block: 'start',
        behavior: 'smooth',
      });
    }
  };

  return (
    <div className='pagination'>
      <div className='pagination__wrap'>
        <div onClick={handleClick} data-back className='pagination__back'>
          <p>
            <span>{'<'}&nbsp;</span> Back
          </p>
        </div>
        <ul>{getPage()}</ul>
        <div onClick={handleClick} data-next className='pagination__back'>
          <p>
            Next <span>&nbsp;{'>'}</span>
          </p>
        </div>
      </div>
    </div>
  );
};

const PaginationList: React.FC<PaginationConf> = ({
  children,
}): JSX.Element => {
  const { page } = useAppSelector((state) => state.device);
  const [searchParams, setSearchParams] = useSearchParams();
  const dispatch = useAppDispatch();
  const pathnamepage = useChangePathname();
  const handleClick = () => {
    pathnamepage('page', children.toString());
    dispatch(changePage({ page: children }));
    document.documentElement.scrollIntoView({
      block: 'start',
      behavior: 'smooth',
    });
  };

  const pagePath = page;

  return (
    <li onClick={handleClick} className={+children === +pagePath ? 'add' : ''}>
      <p className='big'>{children}</p>
    </li>
  );
};

export const usePageNavigate = () => {
  const { oneType } = useAppSelector((state) => state.device);
  const local = useLocation();
  const str = local.pathname + local.search + '';
  const navigate = useNavigate();

  return () => {
    navigate(str, {
      state: {
        id: 1,
        type: oneType,
      },
    });
    document.documentElement.scrollIntoView({
      block: 'start',
      behavior: 'smooth',
    });
  };
};

export const useChangePathname = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  return (name: string, item: string) => {
    searchParams.set(name, item);

    setSearchParams(searchParams);
  };
};
