import { Loading } from '../../components';
import { useAppDispatch, useAppSelector } from '../../store';
import { favoriteLimit } from '../../store/goodsSlices/favoriteSlice';
import './style/LikePagination.scss';

export const LikePagination: React.FC<LikePaginationConf> = ({ limit }) => {
  const { productsLimit, loading } = useAppSelector((state) => state.favorite);
  const dispatch = useAppDispatch();

  async function fetching() {
    const count = productsLimit.length + +limit;
    dispatch(favoriteLimit({ limit: count.toString() }));
  }

  return (
    <div className='likepagination'>
      <div className='likepagination__wrap'>
        <div className='likepagination__btn' onClick={fetching}>
          {loading ? <Loading /> : <p className=''>Show more</p>}
        </div>
      </div>
    </div>
  );
};

interface LikePaginationConf {
  limit: string;
}
