import React from 'react';
import { selectedMine } from '../Shop/Tuner/components/selectedMine';
import './style/LikeTuner.scss';
import { $ } from '../../components';

export const LikeTuner: React.FC<TunerLimittypes> = ({ limit, setLimit }) => {
  const handelrClick = (e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
    const target = e.target as HTMLElement;
    const li = target.closest('li');
    const block = target.closest('.liketuner__limit');
    if (li?.textContent && li) {
      selectedMine(e, 'li');
      setLimit(li.textContent.toString().replace('Count:', ''));
      $('.liketuner__limit ul').remove('add');
    } else if (block) {
      $('.liketuner__limit ul').toggle('add');
      $('.liketuner__limit').toggle('add');
    }
  };

  return (
    <div className='liketuner'>
      <div className='liketuner__row'>
        <div className='liketuner__blocks'>
          <div className='liketuner__block'>
            <div className='liketuner__limit'>
              <div onClick={handelrClick} className='liketuner__number'>
                <p>Limit </p>
                <p className='liketuner__arrow'>{'>'}</p>
              </div>
              <ul>
                <li className={+limit == 4 ? 'add' : ''} onClick={handelrClick}>
                  <p className='mini'>Count: 4</p>
                </li>
                <li className={+limit == 8 ? 'add' : ''} onClick={handelrClick}>
                  <p className='mini'>Count: 8</p>
                </li>
                <li
                  className={+limit == 12 ? 'add' : ''}
                  onClick={handelrClick}
                >
                  <p className='mini'>Count: 12</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

interface TunerLimittypes {
  limit: string;
  setLimit: React.Dispatch<React.SetStateAction<string>>;
}
