export function selectedMine(e: React.MouseEvent<HTMLElement>, el: string) {
  const siblings: HTMLElement[] = [];
  let target = (e.target as HTMLElement).closest(el) as HTMLElement;
  while (target.previousSibling) {
    target = target.previousSibling as HTMLElement;
    target.nodeType == 1 && siblings.push(target);
  }

  target = (e.target as HTMLElement).closest(el) as HTMLElement;
  while (target.nextSibling) {
    target = target.nextSibling as HTMLElement;
    target.nodeType == 1 && siblings.push(target);
  }
  siblings.map((item) => {
    (item as HTMLElement)?.closest(el)?.classList.remove('add');
  });
  (e.target as HTMLElement).closest(el)?.closest(el)?.classList.toggle('add');
}

export function selectedMineAll(e: React.MouseEvent<HTMLElement>, el: string) {
  const sibling = e.target as HTMLElement;

  const parent = sibling.closest('ul');
  if (!sibling.closest('li[data-type="All"]')) {
    sibling.closest(el)?.classList.toggle('add');
    parent?.querySelector('li[data-type="All"]')?.classList.remove('add');
  } else {
    Array.from(parent?.querySelectorAll('li.add') as any).forEach(
      (item: any) => {
        item.classList.remove('add');
      }
    );
  }
  if (!parent?.querySelector('li.add')) {
    parent?.querySelector('li[data-type="All"]')?.classList.add('add');
  }
}
